main {
    padding: 0 15px;
}
.form_btn {
    display: inline-block;
    background-color: #a12d78;
    background-image: linear-gradient(-51deg, rgba(221, 0, 74, 0.93) 0%, rgba(254, 143, 98, 0.93) 100%);
    border: 2px solid #fff;
    padding: 10px 35px;
    color: #fff;
    text-align: center;
    border-radius: 25px;
    transition: .3s all;
    &:hover {
        color: #a12d78;
        background-image: none;
        background-color: #fff;
        border: 2px solid #a12d78;
    }
}

.h2-title {
    font-family: 'MontserratBold', sans-serif;
    color: #3366FF;
    font-size: 42px;
    line-height: 1.5;
    position: relative;
    text-transform: uppercase;
    .sm-block({
        text-align: center;
        font-size: 36px;
    });
    .xs-block({
        text-align: center;
        font-size: 32px;
        line-height: 1.2;
    });
}
.subtitle {
    color: #36f;
    font-weight: 700;
    text-transform: uppercase;
    .sm-block({
        text-align: center;
    });
}
.freak {
    .move_item__right {
        .sm-block({
            order: 2;
        });
    }
    .move_item__left {
        .sm-block({
            order: 1;
        });
    }
}
.header {
    box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.2);
    padding: 15px 0;
    &_wrap {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        min-height: 40px;
        .xs-block({
            text-align: center;
            justify-content: center;
        });
    }
    &_item {
        margin: 5px 20px;
        .xs-block({
            margin: 5px 10px;
        });
    }
    &_logo {
        font-family: 'Pacifico', cursive;
        margin-left: 0;
        position: relative;
    }
    &_tel {
        position: relative;
        padding-left: 25px;
        &::after {
            content: '';
            position: absolute;
            left: 0; top: 0;
            background: url(../img/tel.png) no-repeat;
            width: 17px;
            height: 17px;
            transition: .3s all;
        }
        &:hover {
            text-decoration: underline;
            &::after {
                transform: rotate(30deg);
            }
        }
    }
    &_mail {
        color: #0000FF;
        text-decoration: underline;
        &:hover {
            text-decoration: none;
        }
    }
}
.move {
    margin-top: 110px;
    .sm-block({
        margin-top: 50px;
    });
    &_title {
        &::after {
            content: 'Наши направления';
            position: absolute;
            color: #f7f7f7;
            font-size: 64px;
            top: -48px; left: 0;
            z-index: -1;
            .sm-block({
                content: '';
                display: none;
            });
        }
    }
    &_item {
        margin: 80px 0;
        .sm-block({
            margin: 50px 0;
            justify-content: center;
        });
        .row-flex();
        &__left {
            .col();
            .size(4);
            .size-sm(10);
            .size-xxs(11);
            text-align: center;
        }
        &__right {
            .col();
            .size(8);
            .size-sm(10);
            .size-xxs(11);
            padding: 0 50px;
            line-height: 1.5;
            .sm-block({
                padding: 0 20px;
                margin: 25px 0;
            });
            .xs-block({
                padding: 0px;
            });
            
        }
        &--title {
            display: inline-block;
            font-size: 20px;
            color: #36f;
            font-weight: 700;
            max-width: 350px;
            margin: 0 auto;
        }
        &--img {
            margin: 25px auto 35px;
        }
        &--subtitle {
            margin-top: 25px;
        }
        &--ul {
            display: block;
            margin-top: 15px;
            margin-left: 5px;
        }
        &--li {
            padding-left: 30px;
            position: relative;
            margin: 5px 0;
            &::after {
                content: '';
                position: absolute;
                top: 5px; left: 0;
                background: url(../img/check-mark.png) no-repeat;
                width: 16px;
                height: 16px;
            }
        }
    }
}
.hide {
    height: 10px;
    overflow-y: hidden;
}
.click {
    margin-top: 15px;
    cursor: pointer;
    padding: 5px 15px;
    background: #eee;
    color: #999;
    border-radius: 25px;
    position: relative;
    padding-right: 25px;
    &::after {
        content: '';
        position: absolute;
        top: 12px; right: 10px;
        width: 0; 
        height: 0; 
        border-left: 3px solid transparent;
        border-right: 3px solid transparent;
        border-top: 3px solid #3d3d3d;
    }
}

.spons {
    padding-top: 25px;
    .sm-block({
        padding-top: 0;
    });
    &_title {
        &::after {
            content: 'Наши партнеры';
            position: absolute;
            color: #f7f7f7;
            font-size: 64px;
            top: -48px; left: 0;
            z-index: -1;
            .sm-block({
                content: '';
                display: none;
            });
        }
    }
    &_wrap {
        margin-top: 55px;
        padding: 0 40px;
        position: relative;
    }
}
.slick-arrow {
    cursor: pointer;
    font-size: 0;
    padding: 20px;
    position: absolute;
    
}
.slick-prev {
    background: url(../img/left-arrow.png) no-repeat;
    width: 32px;
    height: 32px;
    top: 50%; left: 0;
    transform: translate(0,-50%);
}
.slick-next {
    background: url(../img/right-arrow.png) no-repeat;
    width: 32px;
    height: 32px;
    top: 50%; right: 0;
    transform: translate(0,-50%);
}
.gallery {
    margin-top: 110px;
    .sm-block({
        margin-top: 55px;
    });
    &_title {
        text-align: right;
        &::after {
            content: 'Галерея';
            position: absolute;
            color: #f7f7f7;
            font-size: 64px;
            top: -48px; right: 0;
            z-index: -1;
            .sm-block({
                content: '';
                display: none;
            });
        }
    }
    &_wrap {
        margin-top: 35px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &_item {
        margin: 20px;
        opacity: 1;
        transition: .3s all;
        overflow: hidden;
        &:hover {
            opacity: .8;
            img {
                transform: scale(5);
                transition: 15s all;
            }
        }
    }
}
.mfp-close {
    cursor: pointer !important;
}
.footer {
    padding: 20px 0;
    margin-top: 50px;
    border: 1px solid #000;
    background-color: #292929;
    color: #fff;
}

.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .8);
    display: none;
}

.popup {
    position: fixed;
    width: 400px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 40px;
    box-sizing: border-box;
    background-color: #292929;
    color: #fff;
    text-align: center;

    &_title {
        font-size: 32px;
        font-weight: 600;
        padding: 10px 0;
    }
    &_descr {
        margin: 10px auto 25px;
        font-weight: 300;
    }
    &_input {
        display: block;
        margin: 15px auto;
        background-color: transparent;
        padding: 15px 45px;
        border-bottom: 1px solid #fff;
        color: #fff;
    }
    &_inner {
        padding: 10px 0;
    }
    &_select {
        display: block;
        text-align: center;
        padding: 0 45px;
        width: 100%;
        height: 35px;
        position: relative;
        color: #fff;
        &::before {
            content: "";
            display: block;
            width: 1px;
            height: 33px;
            background-color: #fff;
            pointer-events: none;
            position: absolute;
            top: 1px;
            right: 80px;
            z-index: 1;
        }
        &::after {
            content: "";
            border-style: solid;
            border-width: 5px 5px 0 5px;
            border-color: #fff transparent transparent transparent;
            pointer-events: none;
            position: absolute;
            top: 50%;
            right: 58px;
            z-index: 1;
            margin-top: -2px;
        }
        .select {
            -webkit-appearance: none;
            -moz-appearance: none;
            width: 100%;
            height: 100%;
            padding: 0px 20px;
            background: none;
            border-radius: 3px;
            border: 1px solid #fff;
            font-size: 14px;
            color: #fff;
            background-color: #292929;
            &::-ms-expand {
                display: none;
            }
            &:focus {
                outline: 0;
                border-color: #2cb0d8;
            }
            &:hover {
                cursor: pointer;
            }

        }
    }
    &_btn {
        cursor: pointer;
        display: inline-block;
        margin: 25px auto 15px;
        font-size: 14px;
        text-transform: uppercase;
        line-height: 19px;
        font-weight: 600;
        letter-spacing: 1px;
        padding: 15px 45px;
        border: 1px solid #fff;
        background-color: transparent;
        color: #fff;
        transition: .3s all;

        &:hover {
            background: #a12d78;
            border: 1px solid #a12d78;
        }
    }
}


.close-popup {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 23px;
    height: 23px;
    cursor: pointer;
}

.close-popup:before {
    content: '';
    background-color: #fff;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    left: -4px;
    transform: rotate(-45deg);
}

.close-popup:after {
    content: '';
    background-color: #fff;
    position: absolute;
    height: 1px;
    width: 31px;
    top: 11px;
    transform: rotate(45deg);
    left: -4px;
}

@media screen and (max-width: 768px) {
    .popup {
        width: 100%;
        height: 100vh;
        font-size: 15px;
    }
    .close-popup {
        top: 35px;
    }
}
.success {
    display: none;
    border: 1px solid green;
    padding: 10px 15px;
}